<template>
    <v-card>
        <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Register</v-toolbar-title>            
            <v-spacer />
        </v-toolbar>
        <v-card-text>
            <div class="subtitle-1">Fill in the form fields below</div>
            <v-form ref="form" v-model="valid" lazy-validation>
                <v-row>                    
                    <v-col cols="12" sm="6" md="6">
                        <v-radio-group v-model="radios" column>
                            <v-radio label="Teacher" value="0"></v-radio>
                            <v-radio label="Principal" value="1"></v-radio>
                            <v-radio label="Supervisor" value="2"></v-radio>
                        </v-radio-group>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                        <v-radio-group v-model="radios" column>
                            <v-radio label="Superintendent" value="3"></v-radio>
                            <v-radio label="Regional Director" value="4"></v-radio>
                            <v-radio label="Secretary of Education" value="5"></v-radio>
                        </v-radio-group>
                    </v-col>
                </v-row>
                <v-text-field label="First Name" v-model="firstName"></v-text-field>
                <v-text-field label="Last Name" v-model="lastName"></v-text-field>
                <v-text-field label="ID#" v-model="id"></v-text-field>
                <v-text-field label="Address" v-model="address"></v-text-field>
                <v-text-field label="Street" v-model="street"></v-text-field>
                <v-text-field label="Town" v-model="town"></v-text-field>
                <v-text-field label="Zip code" v-model="zipcode"></v-text-field>
                <v-text-field label="Email address (.gov)" v-model="emailprimary"></v-text-field>
                <v-text-field label="Email address secondary" v-model="emailsecondary"></v-text-field>
                <v-text-field label="School name" v-model="schoolname"></v-text-field>
                <v-text-field label="School ID#" v-model="schoolid"></v-text-field>
                <v-text-field label="Activity sheet subject" v-model="actsheetsubj"></v-text-field>
                <v-text-field label="School day #: (1-240)" v-model="schoolday"></v-text-field>
                <v-btn :disabled="!valid" color="success" class="mr-4" @click="submit">
                    SUBMIT
                </v-btn>
            </v-form>
        </v-card-text>
    </v-card>
</template>
<script>
  export default {
      data: () => ({
          firstName: '',
          lastName: '',
          id: '',
          address: '',
          street: '',
          town: '',
          zipcode: '',
          emailprimary: '',
          emailsecondary: '',
          schoolname: '',
          schoolid: '',
          actsheetsubj: '',
          schoolday: "0",
          valid: true,
          name: 'RegisterForm',
          row: null,
          radios: "0",
          nameRules: [
              v => !!v || 'Name is required',
              v => (v && v.length <= 10) || 'Name must be less than 10 characters',
          ],
          email: '',
          emailRules: [
              v => !!v || 'E-mail is required',
              v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
          ],
          select: null,
          items: [
              'Item 1',
              'Item 2',
              'Item 3',
              'Item 4',
          ],
          checkbox: false,
      }),

      methods: {
          validate() {
              if (this.$refs.form.validate()) {
                  this.snackbar = true
              }
          },
          reset() {
              this.$refs.form.reset()
          },
          resetValidation() {
              this.$refs.form.resetValidation()
          },
          submit() {
              let url = this.$hostname + '/api/insertregister?code=e0V7XcDF0ovR520k/ulKQm98j8JhpEZmCNTSp9LsZnbnuFRPgcFSpQ==';
              this.$emit('submit');
              this.axios.post(url, {
                  RegisterCode: this.$route.query.code === undefined ? 'CODEFROMURL' : this.$route.query.code,
                  Position: parseInt(this.radios),
                  FirstName: this.firstName,
                  LastName: this.lastName,
                  RegisterId: this.id,
                  Address: this.address,
                  Street: this.street,
                  Town: this.town,
                  Zip: this.zipcode,
                  EmailPrimary: this.emailprimary,
                  EmailSecondary: this.emailsecondary,
                  SchoolName: this.schoolname,
                  ActivitySheetSubject: this.actsheetsubj,
                  SchoolDay: parseInt(this.schoolday)
              }).then(() => {
                  this.$emit('submit');
              });
          }          
      },
  }
</script>

